.BoardCell {
	width: 3em;
	height: 3em;
	position: relative;
}

.BoardCell > .BoardCell-sleeve {
	width: 100%;
	height: 100%;
	background-image: -webkit-radial-gradient(
		transparent 0,
		transparent 1.35em,
		hsl(220, 80%, 40%) 1.35em
	);
	background-image: -o-radial-gradient(
		transparent 0,
		transparent 1.35em,
		hsl(220, 80%, 40%) 1.35em
	);
	background-image: radial-gradient(
		transparent 0,
		transparent 1.35em,
		hsl(220, 80%, 40%) 1.35em
	);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.BoardCell > .BoardCell-piece {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.BoardCell > .BoardCell-piece > .Piece {
	-webkit-animation: 0.25s drop ease-in;
	        animation: 0.25s drop ease-in;
}

@-webkit-keyframes drop {
	from {
		-webkit-transform: translateY(-600%);
		        transform: translateY(-600%);
	}

	to {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes drop {
	from {
		-webkit-transform: translateY(-600%);
		        transform: translateY(-600%);
	}

	to {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}



